import React, { useState, useEffect } from 'react';
import { Box, Typography, Paper, Modal } from '@mui/material';
import WifiIcon from '@mui/icons-material/Wifi';
import ReceiptIcon from '@mui/icons-material/Receipt';
import PersonIcon from '@mui/icons-material/Person';
import { useNavigate } from 'react-router-dom';
import { CircleLoader } from 'react-spinners';

function AcessoRapido() {
  const navigate = useNavigate();

  // Estados para controlar o modal, mensagens de carregamento, cor primária e cor dos ícones
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [loadingMessage, setLoadingMessage] = useState('');
  const [primaryColor, setPrimaryColor] = useState('#28a745'); // Cor padrão para o CircleLoader
  const [iconColor, setIconColor] = useState('#5b5b5b'); // Cor padrão dos ícones

  // Função para buscar cliente enviando o CPF/CNPJ via POST
  const buscarCliente = async (cpfCnpj) => {
    try {
      // Abrir o modal com a mensagem animada
      setLoadingMessage('Aguarde... Carregando Dados Financeiros.');
      setIsModalOpen(true);

      const response = await fetch('https://www.appmax.nexusnerds.com.br/buscar-cliente', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ cnpj_cpf: cpfCnpj }),
      });

      if (!response.ok) {
        throw new Error(`Erro: ${response.statusText}`);
      }

      const data = await response.json();
      console.log('Dados do cliente recebidos:', data);

      // Atualiza o estado com os novos dados
      localStorage.setItem('dadosCliente', JSON.stringify(data));
    } catch (error) {
      console.error('Erro ao buscar o cliente:', error.message);
    } finally {
      // Fechar o modal após a conclusão da solicitação
      setIsModalOpen(false);
    }
  };

  // Função para lidar com o clique em Minhas Faturas
  const handleMinhasFaturasClick = async () => {
    const dadosCliente = JSON.parse(localStorage.getItem('dadosCliente'));
    const cnpjCpf = dadosCliente?.dadosCliente?.['dados_cliente.json']?.cnpj_cpf;

    if (cnpjCpf) {
      // Faz o POST com o CPF/CNPJ quando o usuário clica em "Financeiro"
      await buscarCliente(cnpjCpf);
    } else {
      console.log('CPF/CNPJ não disponível.');
    }
    navigate('/financeiro');
  };

  useEffect(() => {
    const fetchAppSettings = async () => {
      const token = 'ZqFzoCRvPCyzSRAIKPMbnOaLwR6laivSdxcpXiA5'; // Substitua pelo token correto
      try {
        const response = await fetch('https://nocodb.nexusnerds.com.br/api/v2/tables/mw1lsgk4ka13uhs/records', {
          method: 'GET',
          headers: {
            'Content-Type': 'application/json',
            'xc-token': token,
          },
        });

        if (response.ok) {
          const data = await response.json();
          if (data.list.length > 0) {
            const settings = data.list[0];

            // Atualiza a cor primária (CircleLoader) com o valor recebido do NocoDB
            if (settings.primaryColor) {
              setPrimaryColor(settings.primaryColor);
            }

            // Atualiza a cor dos ícones com o valor recebido do NocoDB
            if (settings.iconColor) {
              setIconColor(settings.iconColor);
            }
          }
        } else {
          console.error('Erro ao buscar as configurações do app:', response.statusText);
        }
      } catch (error) {
        console.error('Erro ao buscar as configurações do app:', error);
      }
    };

    fetchAppSettings();
  }, []);

  return (
    <Box sx={{ padding: '20px' }}>
      <Typography variant="h6" sx={{ fontSize: '14px', fontWeight: 'bold', marginBottom: '20px', color: '#5b5b5b' }}>
        Acesso rápido
      </Typography>
      <Box className="acesso-rapido-scroll" sx={{ display: 'flex', overflowX: 'auto', gap: '15px' }}>
        <Paper
          elevation={0}
          sx={{
            minWidth: '120px',
            textAlign: 'center',
            borderRadius: '10px',
            backgroundColor: '#f5f5f5',
            margin: '0',
            cursor: 'pointer'
          }}
          onClick={() => navigate('/wifi')}
        >
          <WifiIcon sx={{ fontSize: 30, color: iconColor }} /> {/* Usando a cor do NocoDB */}
          <Typography sx={{ marginTop: '10px', fontSize: '14px', color: iconColor }}>
            Meu Wi-Fi
          </Typography>
        </Paper>

        <Paper
          elevation={0}
          sx={{
            minWidth: '120px',
            textAlign: 'center',
            borderRadius: '10px',
            backgroundColor: '#f5f5f5',
            margin: '0',
            cursor: 'pointer'
          }}
          onClick={handleMinhasFaturasClick}
        >
          <ReceiptIcon sx={{ fontSize: 30, color: iconColor }} /> {/* Usando a cor do NocoDB */}
          <Typography sx={{ marginTop: '10px', fontSize: '14px', color: iconColor }}>
            Minhas Faturas
          </Typography>
        </Paper>

        <Paper
          elevation={0}
          sx={{
            minWidth: '120px',
            textAlign: 'center',
            borderRadius: '10px',
            backgroundColor: '#f5f5f5',
            margin: '0',
            cursor: 'pointer'
          }}
          onClick={() => navigate('/detalhes')}
        >
          <PersonIcon sx={{ fontSize: 30, color: iconColor }} /> {/* Usando a cor do NocoDB */}
          <Typography sx={{ marginTop: '10px', fontSize: '14px', color: iconColor }}>
            Detalhes de Cliente
          </Typography>
        </Paper>
      </Box>

      <Modal
        open={isModalOpen}
        onClose={() => setIsModalOpen(false)}
        aria-labelledby="loading-modal-title"
      >
        <Box
          sx={{
            position: 'absolute',
            bottom: '20px',
            left: '50%',
            transform: 'translateX(-50%)',
            width: '90%',
            maxWidth: '300px',
            bgcolor: 'background.paper',
            boxShadow: 24,
            p: 2,
            borderRadius: '12px',
            textAlign: 'center',
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
            justifyContent: 'center'
          }}
        >
          {/* Usando o CircleLoader com a cor primária */}
          <CircleLoader className="loading-modal" color={primaryColor} size={25} />
          <Typography id="loading-modal-title" variant="h6" sx={{ marginTop: 2 }}>
            {loadingMessage}
          </Typography>
        </Box>
      </Modal>
    </Box>
  );
}

export default AcessoRapido;
