import React, { useState, useEffect } from 'react';
import { HashRouter as Router, Route, Routes } from 'react-router-dom';
import DashboardMobile from './DashboardMobile';
import Login from './Login';
import Cadastro from './Cadastro';
import DetalhesCliente from './DetalhesCliente';
import Planos from './Planos';
import PlanoDetalhes from './PlanoDetalhes';
import Financeiro from './Financeiro';
import Duvidas from './Duvidas';
import Perfil from './Perfil';
import Footer from './Footer';
import Home from './Home';
import Header from './Header'; // Importa o Header
import MeuWifi from './MeuWiFi';
import TodosOsDispositivos from './TodosOsDispositivos'; // Certifique-se de que o caminho está correto
import TodosHosts from './TodosHosts'; // Importe o componente que exibirá todos os hosts


import './App.css';

function App() {
  const [dadosCliente, setDadosCliente] = useState(null); // Estado para os dados do cliente
  const [footerValue, setFooterValue] = useState(0); // Estado para controlar o valor do Footer
  const [primaryColor, setPrimaryColor] = useState('#28a745'); // Cor padrão
  const [headerIconColor, setHeaderIconColor] = useState('#28a745'); // Cor padrão para os ícones do header
  const [footerIconColor, setFooterIconColor] = useState('#28a745'); // Cor padrão para os ícones do footer


  useEffect(() => {
    // Função para buscar configurações do NocoDB
    const fetchAppSettings = async () => {
      const token = 'ZqFzoCRvPCyzSRAIKPMbnOaLwR6laivSdxcpXiA5'; // Substitua pelo token correto
  
      try {
        const response = await fetch('https://nocodb.nexusnerds.com.br/api/v2/tables/mw1lsgk4ka13uhs/records', {
          method: 'GET',
          headers: {
            'Content-Type': 'application/json',
            'xc-token': token,
          },
        });
  
        if (response.ok) {
          const data = await response.json();
          console.log('Configurações do NocoDB:', data); // Log para verificar os dados recebidos
          if (data.list.length > 0) {
            // Atualiza configurações do app (exemplo: cores)
            const settingsData = data.list[0];
            setPrimaryColor(settingsData.primaryColor || '#28a745');
            setHeaderIconColor(settingsData.headerIconColor || '#28a745');
            setFooterIconColor(settingsData.footerIconColor || '#28a745');
  
            // Atualiza as variáveis CSS dinamicamente
            document.documentElement.style.setProperty('--primary-color', settingsData.primaryColor || '#28a745');
            document.documentElement.style.setProperty('--header-icon-color', settingsData.headerIconColor || '#28a745');
            document.documentElement.style.setProperty('--footer-icon-color', settingsData.footerIconColor || '#28a745');
          }
        } else {
          console.error('Erro ao buscar as configurações do app:', response.statusText);
        }
      } catch (error) {
        console.error('Erro ao buscar as configurações do app:', error);
      }
    };
  
    // Chama a função para buscar configurações
    fetchAppSettings();
  
    // Adicione outras lógicas aqui se necessário (por exemplo, logout automático)
  }, []); // Não adiciona dependências se você quer que isso execute apenas uma vez na montagem
  
  
  const handleFooterChange = (newValue) => {
    setFooterValue(newValue); // Atualiza o valor do Footer quando o onChange é chamado
  };

  return (
    <Router>  {/* Certifique-se de que tudo está dentro do Router */}
      <div className="app-container">
        {/* Renderiza o Header apenas se o usuário estiver logado */}
        {dadosCliente && <Header dadosCliente={dadosCliente} />} {/* Passando dadosCliente corretamente */}

        <Routes>
          {/* A tela de Login não exibe o Header */}
          <Route path="/" element={<Login onLogin={setDadosCliente} />} />
          <Route path="/home" element={<Home dadosCliente={dadosCliente} />} />
          <Route path="/dashboard" element={<DashboardMobile dados={dadosCliente} />} />
          <Route path="/cadastro" element={<Cadastro dados={dadosCliente} />} />
          <Route path="/detalhes" element={<DetalhesCliente dadosCliente={dadosCliente} />} />
          <Route path="/planos" element={<Planos />} />
          <Route path="/planos/:id" element={<PlanoDetalhes />} /> {/* Rota dinâmica */}
          <Route path="/financeiro" element={<Financeiro dados={dadosCliente} />} />
          <Route path="/wifi" element={<MeuWifi dados={dadosCliente} />} />
          <Route path="/duvidas" element={<Duvidas />} />
          <Route path="/perfil" element={<Perfil />} />

          {/* Novas rotas para os dispositivos */}
          <Route path="/dispositivos/:frequencia" element={<TodosOsDispositivos />} />
          <Route path="/dispositivos/ethernet" element={<TodosOsDispositivos frequencia="ethernet" />} />
          <Route path="/todos-hosts" element={<TodosHosts />} />

        </Routes>

        {/* Renderiza o Footer apenas se o usuário estiver logado */}
        {dadosCliente && (
          <Footer
            value={footerValue}
            onChange={handleFooterChange}
            setDados={setDadosCliente} // Passa o setDadosCliente para atualizar os dados com novos dados do cliente
          />
        )}
      </div>
    </Router>
  );
}

export default App;
