import React, { useState, useEffect } from 'react';
import { Container, Typography, Button, TextField, Modal, Box } from '@mui/material';
import { useNavigate } from 'react-router-dom';
import { CircleLoader } from 'react-spinners'; // Importa o CircleLoader do react-spinners
import './Login.css'; // Arquivo CSS personalizado

const formatarCPFouCNPJ = (valor) => {
  valor = valor.replace(/\D/g, ''); // Remove tudo que não for dígito
  if (valor.length <= 11) {
    valor = valor.substring(0, 11);
    valor = valor.replace(/(\d{3})(\d)/, '$1.$2');
    valor = valor.replace(/(\d{3})(\d)/, '$1.$2');
    valor = valor.replace(/(\d{3})(\d{1,2})$/, '$1-$2');
  } else {
    valor = valor.substring(0, 14);
    valor = valor.replace(/^(\d{2})(\d)/, '$1.$2');
    valor = valor.replace(/^(\d{2})\.(\d{3})(\d)/, '$1.$2.$3');
    valor = valor.replace(/\.(\d{3})(\d)/, '.$1/$2');
    valor = valor.replace(/(\d{4})(\d{1,2})$/, '$1-$2');
  }
  return valor;
};

function Login({ onLogin }) {
  const [documento, setDocumento] = useState('');
  const [erro, setErro] = useState('');
  const [loading, setLoading] = useState(false);
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [logoUrl, setLogoUrl] = useState(''); // Estado para armazenar a URL da logo
  const [backgroundUrl, setBackgroundUrl] = useState(''); // Estado para armazenar a URL do background
  const [animationType, setAnimationType] = useState('none'); // Estado para armazenar o tipo de animação
  const [primaryColor, setPrimaryColor] = useState('#28a745'); // Valor padrão da cor primária
  const [clienteInfo, setClienteInfo] = useState(null);
  const [dados, setDados] = useState(null);




  const navigate = useNavigate();

  // Função para atualizar o documento e aplicar a máscara automaticamente
  const handleDocumentoChange = (e) => {
    const valor = e.target.value;
    setDocumento(formatarCPFouCNPJ(valor));
  };

  useEffect(() => {
    const token = 'ZqFzoCRvPCyzSRAIKPMbnOaLwR6laivSdxcpXiA5'; // Substitua pelo token correto
  
    // Função para buscar dados da API
    const fetchLogoAndBackground = async () => {
      try {
        const response = await fetch('https://nocodb.nexusnerds.com.br/api/v2/tables/mw1lsgk4ka13uhs/records', {
          method: 'GET',
          headers: {
            'Content-Type': 'application/json',
            'xc-token': token,
          },
        });
  
        if (response.ok) {
          const data = await response.json();
          console.log('Dados recebidos da API:', data); // Log para verificar os dados recebidos da API
  
          if (data.list.length > 0) {
            let newLogoUrl = null;
            let newBackgroundUrl = null;
            let newAnimationType = 'none';
            let newPrimaryColor = '#28a745'; // Cor padrão
  
            // Atualiza a logo
            if (data.list[0]['URL  - LOGO EMPRESA'] && data.list[0]['URL  - LOGO EMPRESA'].length > 0) {
              const logoData = data.list[0]['URL  - LOGO EMPRESA'][0];
              newLogoUrl = `https://nocodb.nexusnerds.com.br/${logoData.signedPath}`;
              setLogoUrl(prevLogoUrl => prevLogoUrl !== newLogoUrl ? newLogoUrl : prevLogoUrl);
            }
  
            // Atualiza o background
            if (data.list[0]['URL - BACKGROUND LOGIN'] && data.list[0]['URL - BACKGROUND LOGIN'].length > 0) {
              const backgroundData = data.list[0]['URL - BACKGROUND LOGIN'][0];
              newBackgroundUrl = `https://nocodb.nexusnerds.com.br/${backgroundData.signedPath}`;
              setBackgroundUrl(prevBackgroundUrl => prevBackgroundUrl !== newBackgroundUrl ? newBackgroundUrl : prevBackgroundUrl);
            }
  
            // Atualiza a animação
            newAnimationType = data.list[0].animationType || 'none';
            setAnimationType(prevAnimationType => prevAnimationType !== newAnimationType ? newAnimationType : prevAnimationType);
  
            // Atualiza a cor primária
            newPrimaryColor = data.list[0].primaryColor || '#28a745';
            setPrimaryColor(prevPrimaryColor => prevPrimaryColor !== newPrimaryColor ? newPrimaryColor : prevPrimaryColor);
  
            // Salva os dados no localStorage
            const newAppSettings = {
              logoUrl: newLogoUrl,
              backgroundUrl: newBackgroundUrl,
              animationType: newAnimationType,
              primaryColor: newPrimaryColor,
            };
            localStorage.setItem('appSettings', JSON.stringify(newAppSettings));
            console.log('Dados salvos no localStorage:', newAppSettings);
          }
        } else {
          console.error('Erro ao buscar a logo, background e animação:', response.statusText);
        }
      } catch (error) {
        console.error('Erro ao buscar a logo, background e animação:', error);
      }
    };
  
    // Chama a função imediatamente
    fetchLogoAndBackground();
  
    // Configura o interval para verificar a API a cada 5 segundos
    const intervalId = setInterval(fetchLogoAndBackground, 5000); // 5000 ms = 5 segundos
  
    // Limpa o interval quando o componente for desmontado
    return () => clearInterval(intervalId);
    const savedData = localStorage.getItem('dadosCliente');
    if (savedData) {
      setDados(JSON.parse(savedData));
    }
  }, []);
  
  
  
  
  

  const handleSubmit = async (event) => {
    event.preventDefault();
    setErro('');
    setLoading(true);
    setIsModalOpen(true); // Abre o modal

    const documentoFormatado = documento.trim();

    if (documentoFormatado.length !== 14 && documentoFormatado.length !== 18) {
      setErro('CPF ou CNPJ inválido.');
      setLoading(false);
      setIsModalOpen(false); // Fecha o modal em caso de erro
      return;
    }

    try {
      const response = await fetch('https://www.appmax.nexusnerds.com.br/buscar-cliente', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ cnpj_cpf: documentoFormatado }),
      });

      if (response.ok) {
        const data = await response.json();

        localStorage.setItem('cnpj_cpf', documentoFormatado); 
        localStorage.setItem('dadosCliente', JSON.stringify(data));
        setClienteInfo(data);
        console.log("Dados do USER salvos:", data);

        onLogin(data);
        navigate('/home');
      } else {
        const errorData = await response.json();
        setErro(errorData.message);
      }
    } catch (error) {
      console.error('Erro ao buscar o cliente:', error.message);
      setErro(`Erro ao buscar o cliente: ${error.message}`);
    } finally {
      setLoading(false);
      setIsModalOpen(false); // Fecha o modal quando o processamento estiver concluído
    }
  };





  
return (
  <div
    className="login-background"
    style={{ 
      backgroundImage: backgroundUrl ? `url(${backgroundUrl})` : 'none', // Adiciona a URL do background ou 'none' como fallback
      backgroundRepeat: 'no-repeat',
      backgroundSize: 'cover',
      backgroundPosition: 'center'
    }}
  >
    {/* Renderiza a logo se ela estiver disponível */}
    {logoUrl && (
  <div className={`logo-container ${animationType}`}>
        <img src={logoUrl} alt="Logo da Empresa" className="logo-img" />
      </div>
    )}

    <Container maxWidth="xs" className="login-container">
      <Typography variant="body1" className="login-instruction" sx={{ color: '#fff', marginBottom: '20px' }}>
        Digite seu CPF ou CNPJ para continuar
      </Typography>

      <form onSubmit={handleSubmit}>
        <TextField
          placeholder="CPF ou CNPJ"
          type="text"
          value={documento}
          onChange={handleDocumentoChange}
          fullWidth
          margin="normal"
          variant="standard"
          InputProps={{
            disableUnderline: false,
            classes: {
              input: 'inputField'
            },
          }}
          inputProps={{
            style: { textAlign: 'center', color: 'white' }
          }}
          className="login-input"
          required
        />

        {erro && (
          <Typography color="error" className="login-error">
            {erro}
          </Typography>
        )}

        <Button
          type="submit"
          variant="contained"
          fullWidth
          className="login-button"
          sx={{
            backgroundColor: '#fff',
            color: '#01d636',
            '&:hover': {
              backgroundColor: '#f0f0f0',
            },
            padding: '10px 0',
            marginTop: '20px',
            fontSize: '16px',
            borderRadius: '8px',
          }}
          disabled={loading}
        >
          Continuar
        </Button>

        <Button
          variant="outlined"
          fullWidth
          className="register-button"
          sx={{
            color: '#fff',
            borderColor: '#fff',
            backgroundColor: 'transparent',
            marginTop: '10px',
            fontSize: '14px',
            borderRadius: '8px',
            '&:hover': {
              borderColor: '#fff',
              backgroundColor: 'rgba(255, 255, 255, 0.1)',
            },
          }}
          onClick={() => console.log('Cadastrar-se agora!')}
        >
          Cadastre-se agora!
        </Button>
      </form>
    </Container>

    {/* Modal para o carregamento */}
    <Modal
      open={isModalOpen}
      onClose={() => setIsModalOpen(false)}
      aria-labelledby="loading-modal-title"
    >
      <Box
        sx={{
          position: 'absolute',
          top: '50%',
          left: '50%',
          transform: 'translate(-50%, -50%)',
          bgcolor: 'background.paper',
          boxShadow: 24,
          p: 4,
          borderRadius: '8px',
          textAlign: 'center',
          display: 'flex',
          flexDirection: 'column',
          alignItems: 'center',
          justifyContent: 'center'
        }}
      >
          <CircleLoader className="loading-modal" color={primaryColor} size={25} />
          <Typography id="loading-modal-title" variant="h6" sx={{ mt: 2 }}>
          Aguarde... Carregando Dados.
        </Typography>
      </Box>
    </Modal>
  </div>
);

  
}

export default Login;
