/* global google */
import React, { useState, useEffect, useRef } from 'react';
import { Box, TextField, Typography, Grid, Paper } from '@mui/material';

function Perfil() {
  const [latitude, setLatitude] = useState('');
  const [longitude, setLongitude] = useState('');
  const [nome, setNome] = useState('');
  const [cpfCnpj, setCpfCnpj] = useState('');
  const [telefoneCelular, setTelefoneCelular] = useState('');
  const [cep, setCep] = useState('');
  const [numero, setNumero] = useState('');
  const [endereco, setEndereco] = useState('');
  const [bairro, setBairro] = useState('');
  const [complemento, setComplemento] = useState('');
  const [cidade, setCidade] = useState('');
  const [whatsapp, setWhatsapp] = useState('');
  const [email, setEmail] = useState('');
  const [primaryColor, setPrimaryColor] = useState('green'); // Default color
  const [iconCasaConectadaUrl, setIconCasaConectadaUrl] = useState('https://i.ibb.co/JqkK1WF/casa.png'); // Estado para a URL do ícone

  const mapRef = useRef(null);


// Carregar os dados do localStorage e buscar a cor primária ao montar o componente
useEffect(() => {
  // Função para buscar a cor primária e ícones do NocoDB
  const fetchPrimaryColorAndIcons = async () => {
    const token = 'ZqFzoCRvPCyzSRAIKPMbnOaLwR6laivSdxcpXiA5'; // Substitua pelo token correto
    try {
      const response = await fetch('https://nocodb.nexusnerds.com.br/api/v2/tables/mw1lsgk4ka13uhs/records', {
        method: 'GET',
        headers: {
          'Content-Type': 'application/json',
          'xc-token': token,
        },
      });

      if (response.ok) {
        const data = await response.json();
        if (data.list.length > 0) {
          const settings = data.list[0];

          // Define a cor primária
          if (settings.primaryColor) {
            setPrimaryColor(settings.primaryColor);
          }

          // Atualiza o estado com a URL do ícone "IconCasaConectada"
          if (settings.IconCasaConectada && settings.IconCasaConectada.length > 0) {
            setIconCasaConectadaUrl(`https://nocodb.nexusnerds.com.br/${settings.IconCasaConectada[0].signedPath}`);
          }
        }
      } else {
        console.error('Erro ao buscar as configurações:', response.statusText);
      }
    } catch (error) {
      console.error('Erro ao buscar as configurações:', error);
    }
  };

  // Função para carregar os dados do localStorage
  const loadClientData = () => {
    const storedData = localStorage.getItem('dadosCliente');
    if (storedData) {
      try {
        const dadosCliente = JSON.parse(storedData);
        console.log('Dados Cliente:', dadosCliente);

        // Verificar se dadosCliente e dadosCliente.dadosCliente existem
        if (dadosCliente && dadosCliente.dadosCliente && dadosCliente.dadosCliente['dados_cliente.json']) {
          const cliente = dadosCliente.dadosCliente['dados_cliente.json'];

          // Preencher os estados com os valores do localStorage
          setNome(cliente.razao || '');
          setCpfCnpj(cliente.cnpj_cpf || '');
          setTelefoneCelular(cliente.telefone_celular || '');
          setCep(cliente.cep || '');
          setNumero(cliente.numero || '');
          setEndereco(cliente.endereco || '');
          setBairro(cliente.bairro || '');
          setComplemento(cliente.complemento || '');
          setWhatsapp(cliente.whatsapp || '');
          setEmail(cliente.email || '');
          setLatitude(cliente.latitude || ''); // Carregar a latitude do cliente
          setLongitude(cliente.longitude || ''); // Carregar a longitude do cliente

          // Verificar se cidade é igual a "3169" e mudar para "Viana"
          const cidade = cliente.cidade === '3169' ? 'Viana' : (cliente.cidade || '');
          setCidade(cidade);
        }
      } catch (error) {
        console.error('Erro ao carregar os dados do cliente do localStorage:', error);
      }
    }
  };

  // Executar ambas as funções inicialmente
  fetchPrimaryColorAndIcons();
  loadClientData();

  // Configurar intervalo para verificar a API a cada 5 segundos
  const intervalId = setInterval(fetchPrimaryColorAndIcons, 5000);

  // Limpar o intervalo ao desmontar o componente
  return () => {
    clearInterval(intervalId);
  };
}, []); // Deixe o array de dependências vazio para executar apenas uma vez quando o componente for montado







// Inicializa o mapa com um marcador personalizado
useEffect(() => {
  if (latitude && longitude && iconCasaConectadaUrl) { // Certifique-se de que a URL do ícone esteja disponível
    const lat = parseFloat(latitude);
    const lng = parseFloat(longitude);

    // Inicialize o mapa
    const map = new google.maps.Map(mapRef.current, {
      center: { lat, lng },
      zoom: 19,
      disableDefaultUI: true, // Remove todos os controles padrão
      mapTypeId: 'hybrid', // Alterar para 'roadmap', 'hybrid', ou 'terrain' conforme necessário
      zoomControl: false,
      mapTypeControl: false,
      scaleControl: false,
      streetViewControl: false,
      rotateControl: false,
      fullscreenControl: false,
      minZoom: 23, // Define o nível mínimo de zoom permitido
      maxZoom: 23, // Define o nível máximo de zoom permitido
      gestureHandling: 'none', // Desativa zoom via scroll e gestos
    });

    // Adicione um marcador personalizado
    new google.maps.Marker({
      position: { lat, lng },
      map,
      icon: {
        url: iconCasaConectadaUrl || 'https://i.ibb.co/JqkK1WF/casa.png', // Use a URL do NocoDB ou o padrão
        scaledSize: new google.maps.Size(40, 40), // Ajuste o tamanho do ícone
      },
    });
  }
}, [latitude, longitude, iconCasaConectadaUrl]); // Inclua iconCasaConectadaUrl nas dependências





  return (
    <Box sx={{ padding: '20px', minHeight: '100vh', boxSizing: 'border-box', overflowY: 'auto' }}>
      <Typography variant="h5" sx={{ fontWeight: 'bold', marginBottom: '20px' }}>
        Perfil
      </Typography>

      <Grid container spacing={1.7}>
        {/* Primeira linha: Nome */}
        <Grid item xs={12}>
          <TextField
            label="Razão Social/Nome"
            fullWidth
            variant="standard"
            value={nome}
            disabled // Desabilita o campo para edição

            InputProps={{
              disableUnderline: false, // Apenas a linha inferior aparece
              style: { borderBottom: `2px solid ${primaryColor}` }, // Linha inferior usando primaryColor
            }}
            InputLabelProps={{
              sx: {
                color: 'green', // Cor do label
                '&.Mui-focused': {
                  color: 'green', // Cor do label ao focar
                },
              },
            }}
          />
        </Grid>

        {/* Segunda linha: CPF e Telefone */}
        <Grid item xs={6}>
          <TextField
            label="CNPJ/CPF"
            fullWidth
            variant="standard"
            value={cpfCnpj}
            disabled // Desabilita o campo para edição

            InputProps={{
              disableUnderline: false,
              style: { borderBottom: `2px solid ${primaryColor}` }, // Linha inferior usando primaryColor
            }}
            InputLabelProps={{
              sx: {
                color: 'green',
                '&.Mui-focused': {
                  color: 'green',
                },
              },
            }}
          />
        </Grid>
        <Grid item xs={6}>
          <TextField
            label="Telefone Celular"
            fullWidth
            variant="standard"
            value={telefoneCelular}
            disabled // Desabilita o campo para edição

            InputProps={{
              disableUnderline: false,
              style: { borderBottom: `2px solid ${primaryColor}` }, // Linha inferior usando primaryColor
            }}
            InputLabelProps={{
              sx: {
                color: 'green',
                '&.Mui-focused': {
                  color: 'green',
                },
              },
            }}
          />
        </Grid>

        {/* WhatsApp */}
        <Grid item xs={6}>
          <TextField
            label="Whatsapp"
            fullWidth
            variant="standard"
            value={whatsapp}
            disabled // Desabilita o campo para edição

            InputProps={{
              disableUnderline: false,
              style: { borderBottom: `2px solid ${primaryColor}` }, // Linha inferior usando primaryColor
            }}
            InputLabelProps={{
              sx: {
                color: 'green',
                '&.Mui-focused': {
                  color: 'green',
                },
              },
            }}
          />
        </Grid>

        {/* Email Principal */}
        <Grid item xs={6}>
          <TextField
            label="Email Principal"
            fullWidth
            variant="standard"
            value={email}
            disabled // Desabilita o campo para edição

            InputProps={{
              disableUnderline: false,
              style: { borderBottom: `2px solid ${primaryColor}` }, // Linha inferior usando primaryColor
            }}
            InputLabelProps={{
              sx: {
                color: 'green',
                '&.Mui-focused': {
                  color: 'green',
                },
              },
            }}
          />
        </Grid>

        {/* Endereço */}
        <Grid item xs={6}>
          <TextField
            label="CEP"
            fullWidth
            variant="standard"
            value={cep}
            disabled // Desabilita o campo para edição

            InputProps={{
              disableUnderline: false,
              style: { borderBottom: `2px solid ${primaryColor}` }, // Linha inferior usando primaryColor
            }}
            InputLabelProps={{
              sx: {
                color: 'green',
                '&.Mui-focused': {
                  color: 'green',
                },
              },
            }}
          />
        </Grid>
        <Grid item xs={6}>
          <TextField
            label="Nº"
            fullWidth
            variant="standard"
            value={numero}
            disabled // Desabilita o campo para edição

            InputProps={{
              disableUnderline: false,
              style: { borderBottom: `2px solid ${primaryColor}` }, // Linha inferior usando primaryColor
            }}
            InputLabelProps={{
              sx: {
                color: 'green',
                '&.Mui-focused': {
                  color: 'green',
                },
              },
            }}
          />
        </Grid>

        {/* Restante do endereço */}
        <Grid item xs={12}>
          <TextField
            label="Endereço"
            fullWidth
            variant="standard"
            value={endereco}
            disabled // Desabilita o campo para edição

            InputProps={{
              disableUnderline: false,
              style: { borderBottom: `2px solid ${primaryColor}` }, // Linha inferior usando primaryColor
            }}
            InputLabelProps={{
              sx: {
                color: 'green',
                '&.Mui-focused': {
                  color: 'green',
                },
              },
            }}
          />
        </Grid>
        <Grid item xs={4}>
          <TextField
            label="Bairro"
            fullWidth
            variant="standard"
            value={bairro}
            disabled // Desabilita o campo para edição

            InputProps={{
              disableUnderline: false,
              style: { borderBottom: `2px solid ${primaryColor}` }, // Linha inferior usando primaryColor
            }}
            InputLabelProps={{
              sx: {
                color: 'green',
                '&.Mui-focused': {
                  color: 'green',
                },
              },
            }}
          />
        </Grid>
        <Grid item xs={4}>
          <TextField
            label="Complemento"
            fullWidth
            variant="standard"
            value={complemento}
            disabled // Desabilita o campo para edição

            InputProps={{
              disableUnderline: false,
              style: { borderBottom: `2px solid ${primaryColor}` }, // Linha inferior usando primaryColor
            }}
            InputLabelProps={{
              sx: {
                color: 'green',
                '&.Mui-focused': {
                  color: 'green',
                },
              },
            }}
          />
        </Grid>
        <Grid item xs={4}>
          <TextField
            label="Cidade"
            fullWidth
            variant="standard"
            value={cidade}
            disabled // Desabilita o campo para edição

            InputProps={{
              disableUnderline: false,
              style: { borderBottom: `2px solid ${primaryColor}` }, // Linha inferior usando primaryColor
            }}
            InputLabelProps={{
              sx: {
                color: 'green',
                '&.Mui-focused': {
                  color: 'green',
                },
              },
            }}
          />
        </Grid>


        {/* Seção do Mapa */}
        <Grid item xs={12}>
          <Typography variant="h6" sx={{ marginBottom: '5px' }}>
            Mapa:
          </Typography>
          <Paper sx={{ height: '210px', backgroundColor: '#e0e0e0', overflow: 'hidden', borderRadius: '10px' }}>
            {/* Div para o mapa */}
            <div ref={mapRef} style={{ width: '100%', height: '100%', borderRadius: '10px' }}></div>
          </Paper>
        </Grid>
      </Grid>
    </Box>
  );
}

export default Perfil;
