import React, { useState, useEffect  } from 'react';
import { BottomNavigation, BottomNavigationAction, Modal, Box, Typography } from '@mui/material';
import { Home, Description, AttachMoney, HelpOutline, Person } from '@mui/icons-material';
import { useNavigate } from 'react-router-dom';
import { CircleLoader } from 'react-spinners'; // Certifique-se de importar o loader

function Footer({ value, onChange, setDados }) {
  const navigate = useNavigate();
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [loadingMessage, setLoadingMessage] = useState('');
  const [primaryColor, setPrimaryColor] = useState('#28a745'); // Cor padrão
  const [footerIconColor, setFooterIconColor] = useState('#28a745'); // Cor padrão


  // Função para buscar cliente enviando o CPF/CNPJ via POST
  const buscarCliente = async (cpfCnpj) => {
    try {
      // Abrir o modal com a mensagem animada
      setLoadingMessage('Aguarde... Carregando Dados Financeiros.');
      setIsModalOpen(true);

      const response = await fetch('https://www.appmax.nexusnerds.com.br/buscar-cliente', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ cnpj_cpf: cpfCnpj }),
      });

      if (!response.ok) {
        throw new Error(`Erro: ${response.statusText}`);
      }

      const data = await response.json();
      console.log('Dados do cliente recebidos:', data);

      // Atualiza o estado com os novos dados
      setDados(data);
      // Atualiza o localStorage com os novos dados
      localStorage.setItem('dadosCliente', JSON.stringify(data));
    } catch (error) {
      console.error('Erro ao buscar o cliente:', error.message);
    } finally {
      // Fechar o modal após a conclusão da solicitação
      setIsModalOpen(false);
    }
  };
  const handleNavigation = async (newValue) => {
    // Verifica se onChange é uma função antes de chamá-la
    if (typeof onChange === 'function') {
        onChange(newValue);
    } else {
        console.error('onChange não é uma função');
    }

    const cnpjCpf = localStorage.getItem('cnpj_cpf'); // Recupera o CPF/CNPJ do localStorage

    switch (newValue) {
        case 0:
            navigate('/home');
            break;
        case 1:
            navigate('/planos');
            break;
        case 2:
            if (cnpjCpf) {
                // Faz o POST com o CPF/CNPJ quando o usuário clica em "Financeiro"
                await buscarCliente(cnpjCpf);
            } else {
                console.log('CPF/CNPJ não disponível.');
            }
            navigate('/financeiro');
            break;
        case 3:
            navigate('/duvidas');
            break;
        case 4:
            navigate('/perfil');
            break;
        default:
            navigate('/dashboard');
            break;
    }
};



  useEffect(() => {
    // Função para buscar as configurações do NocoDB
    const fetchAppSettings = async () => {
      const token = 'ZqFzoCRvPCyzSRAIKPMbnOaLwR6laivSdxcpXiA5'; // Substitua pelo token correto
      try {
        const response = await fetch('https://nocodb.nexusnerds.com.br/api/v2/tables/mw1lsgk4ka13uhs/records', {
          method: 'GET',
          headers: {
            'Content-Type': 'application/json',
            'xc-token': token,
          },
        });

        if (response.ok) {
          const data = await response.json();
          if (data.list.length > 0) {
            const settings = data.list[0];

            // Atualiza a cor primária (CircleLoader) com o valor recebido do NocoDB
            if (settings.primaryColor) {
              setPrimaryColor(settings.primaryColor);
            }

            // Atualiza a cor dos ícones do footer com o valor recebido do NocoDB
            if (settings.footerIconColor) {
              setFooterIconColor(settings.footerIconColor);
            }
          }
        } else {
          console.error('Erro ao buscar as configurações do app:', response.statusText);
        }
      } catch (error) {
        console.error('Erro ao buscar as configurações do app:', error);
      }
    };

    fetchAppSettings();
  }, []);





  return (
    <>
        <BottomNavigation
          value={value}
          onChange={(event, newValue) => handleNavigation(newValue)} // Usa a função handleNavigation
          showLabels
          sx={{
            position: 'fixed', // Mantém o footer fixo
            bottom: 0,
            width: '100%',
            backgroundColor: '#e0e0e0',
            borderTopLeftRadius: '20px',
            borderTopRightRadius: '20px',
            boxShadow: '0px -2px 10px rgba(0, 0, 0, 0.1)',
          }}
        >

        <BottomNavigationAction
          label={<span className="footer-label">Início</span>}
          icon={<Home className="footer-icon" style={{ color: footerIconColor }} />}
        />
        <BottomNavigationAction
          label={<span className="footer-label">Planos</span>}
          icon={<Description className="footer-icon" style={{ color: footerIconColor }} />}
        />
        <BottomNavigationAction
          label={<span className="footer-label">Financeiro</span>}
          icon={<AttachMoney className="footer-icon" style={{ color: footerIconColor }} />}
        />
        <BottomNavigationAction
          label={<span className="footer-label">Dúvidas</span>}
          icon={<HelpOutline className="footer-icon" style={{ color: footerIconColor }} />}
        />
        <BottomNavigationAction
          label={<span className="footer-label">Perfil</span>}
          icon={<Person className="footer-icon" style={{ color: footerIconColor }} />}
        />
      </BottomNavigation>

      {/* Modal para exibir a mensagem animada */}
      <Modal
        open={isModalOpen}
        onClose={() => setIsModalOpen(false)}
        aria-labelledby="loading-modal-title"
      >
        <Box
          sx={{
            position: 'absolute',
            bottom: '20px',
            left: '50%',
            transform: 'translateX(-50%)',
            width: '90%',
            maxWidth: '300px',
            bgcolor: 'background.paper',
            boxShadow: 24,
            p: 2,
            borderRadius: '12px',
            textAlign: 'center',
            display: 'flex',
            flexDirection: 'column', // Adicionado para alinhar o loader e o texto em coluna
            alignItems: 'center', // Alinha horizontalmente ao centro
            justifyContent: 'center' // Alinha verticalmente ao centro
          }}
        >
          {/* Usando o CircleLoader */}
          <CircleLoader className="loading-modal" color={primaryColor} size={25} />

          <Typography id="loading-modal-title" variant="h6" sx={{ marginTop: 2 }}>
            {loadingMessage}
          </Typography>
        </Box>
      </Modal>
    </>
  );
}

export default Footer;