// Header.js
import React, { useState, useEffect } from 'react';
import { Box, Typography, IconButton, Modal, CircularProgress } from '@mui/material';
import { Notifications, ExitToApp } from '@mui/icons-material';
import { useNavigate } from 'react-router-dom';
import { CircleLoader } from 'react-spinners';
import useFetchAppSettings from './useFetchAppSettings'; // Importe o Hook personalizado

function Header({ dadosCliente }) {
  const navigate = useNavigate();
  const [isLoggingOut, setIsLoggingOut] = useState(false); 
  const [logoUrl, setLogoUrl] = useState(''); // Estado para armazenar a URL da logo
  const [primaryColor, setPrimaryColor] = useState('#28a745'); // Corrigido: Estado para armazenar a cor primária
  const { primaryColor: fetchedPrimaryColor } = useFetchAppSettings(); // Use o Hook aqui

  // Função para formatar o nome
  const formatarNome = (nomeCompleto) => {
    if (!nomeCompleto) return 'Cliente';
    const primeiroNome = nomeCompleto.split(' ')[0];
    return primeiroNome.charAt(0).toUpperCase() + primeiroNome.slice(1).toLowerCase();
  };

  const nomeCliente = formatarNome(
    dadosCliente?.dadosCliente?.['dados_cliente.json']?.razao ||
    dadosCliente?.dadosCliente?.['dados_cliente.json']?.fantasia ||
    'Cliente'
  );

  // Função de logout e limpeza de cache
  const handleLogout = async () => {
    setIsLoggingOut(true);

    try {
      localStorage.clear();

      setTimeout(() => {
        setIsLoggingOut(false);
        navigate('/'); // Redireciona para a tela de login
        window.location.reload();
      }, 500); 
    } catch (error) {
      console.error('Erro durante o logout:', error);
      setIsLoggingOut(false);
    }
  };

  useEffect(() => {
    // Lógica para logout automático após 1 hora
    const logoutTimer = setTimeout(handleLogout, 3600000); // 1 hora = 3600000ms

    const resetTimer = () => {
      clearTimeout(logoutTimer);
      setTimeout(handleLogout, 3600000);
    };

    // Eventos de atividade do usuário
    window.addEventListener('mousemove', resetTimer);
    window.addEventListener('keydown', resetTimer);
    window.addEventListener('scroll', resetTimer);
    window.addEventListener('click', resetTimer);

    // Função para buscar a logo e as cores da API
    const token = 'ZqFzoCRvPCyzSRAIKPMbnOaLwR6laivSdxcpXiA5'; // Substitua pelo token correto

    const fetchLogo = async () => {
      try {
        const response = await fetch('https://nocodb.nexusnerds.com.br/api/v2/tables/mw1lsgk4ka13uhs/records', {
          method: 'GET',
          headers: {
            'Content-Type': 'application/json',
            'xc-token': token,
          },
        });

        if (response.ok) {
          const data = await response.json();
          if (data.list.length > 0) {
            const settings = data.list[0];

            // Atualiza a URL da logo
            if (settings['URL  - LOGO EMPRESA'] && settings['URL  - LOGO EMPRESA'].length > 0) {
              const logoData = settings['URL  - LOGO EMPRESA'][0];
              const logoUrl = `https://nocodb.nexusnerds.com.br/${logoData.signedPath}`;
              
              setLogoUrl(prevLogoUrl => {
                if (prevLogoUrl !== logoUrl) {
                  return logoUrl;
                }
                return prevLogoUrl;
              });
            }

            // Atualiza a cor primária
            if (settings.primaryColor) {
              setPrimaryColor(settings.primaryColor);
            }
          }
        } else {
          console.error('Erro ao buscar a logo e as configurações:', response.statusText);
        }
      } catch (error) {
        console.error('Erro ao buscar a logo e as configurações:', error);
      }
    };

    // Chama a função para buscar a logo e a cor inicialmente
    fetchLogo();

    // Configura o interval para verificar a API a cada 5 segundos
    const intervalId = setInterval(fetchLogo, 5000); // 5000 ms = 5 segundos

    // Limpeza quando o componente é desmontado
    return () => {
      clearTimeout(logoutTimer);
      clearInterval(intervalId); // Limpa o intervalo
      window.removeEventListener('mousemove', resetTimer);
      window.removeEventListener('keydown', resetTimer);
      window.removeEventListener('scroll', resetTimer);
      window.removeEventListener('click', resetTimer);
    };
  }, [navigate]);

  return (
    <Box sx={{ background: '#fff', padding: '20px', display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
      {/* Logotipo e saudação */}
      <Box sx={{ display: 'flex', alignItems: 'center', cursor: 'pointer' }} onClick={() => navigate('/home')}>
        {/* Renderiza a logo obtida da API */}
        {logoUrl && (
          <img
            src={logoUrl}
            alt="Logo"
            style={{ width: '40px', marginRight: '10px' }} // Ajuste a largura conforme necessário
          />
        )}
        <Box>
          <Typography variant="h6" sx={{ fontWeight: 'bold' }}>
            Olá, {nomeCliente}!
          </Typography>
          <Typography variant="body2" sx={{ color: '#6c757d' }}>
            Como Podemos te Ajudar?
          </Typography>
        </Box>
      </Box>


      {/* Ícones de notificações e logout */}
      <Box className="icon-container">
          {isLoggingOut ? (
            <CircularProgress size={24} color="inherit" />
          ) : (
            <>
              <IconButton className="icon-button">
                <Notifications />
              </IconButton>
              <IconButton className="icon-button" onClick={handleLogout}>
                <ExitToApp />
              </IconButton>
            </>
          )}
        </Box>

      {/* Modal de carregamento */}
      <Modal
        open={isLoggingOut}
        aria-labelledby="logout-loading-modal"
        aria-describedby="logout-processing-description"
      >
        <Box
          sx={{
            position: 'absolute',
            top: '50%',
            left: '50%',
            transform: 'translate(-50%, -50%)',
            bgcolor: 'background.paper',
            boxShadow: 24,
            p: 4,
            borderRadius: 2,
            textAlign: 'center',
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
            justifyContent: 'center'
          }}
        >
          <CircleLoader className="loading-modal" color={primaryColor} size={25} />
          <Typography variant="h6" sx={{ mt: 2 }}>
            Saindo... Por favor, aguarde.
          </Typography>
        </Box>
      </Modal>
    </Box>
  );
}

export default Header;
