import React, { useEffect, useState } from 'react';
import { Box, Typography, Grid, Paper, Button } from '@mui/material';
import { ArrowBack, SignalCellularAlt, Wifi } from '@mui/icons-material';
import { useNavigate } from 'react-router-dom';

function DetalhesCliente() {
  const [clientData, setClientData] = useState([]); // Iniciando clientData como array vazio
  const [idContrato, setIdContrato] = useState(null); // Armazena o ID do contrato
  const navigate = useNavigate();

  // Função para formatar o nome
  const formatarNome = (nomeCompleto) => {
    if (!nomeCompleto) return 'Cliente';
    const primeiroNome = nomeCompleto.split(' ')[0];
    return primeiroNome.charAt(0).toUpperCase() + primeiroNome.slice(1).toLowerCase();
  };

  // Função para carregar e exibir os dados do localStorage
  const mostrarDadosDoLocalStorage = () => {
    const storedData = localStorage.getItem('dadosCliente');
    if (storedData) {
      try {
        const dadosCliente = JSON.parse(storedData);
        console.log('🔍 DADOS COMPLETOS SALVOS NO LOCALSTORAGE:', dadosCliente);

        // Verificando se existe a chave `data`, que é o array com os dados
        if (dadosCliente && dadosCliente.data && Array.isArray(dadosCliente.data)) {
          console.log('✅ Estrutura de dados encontrada no localStorage');
          setClientData(dadosCliente.data); // Carrega os dados da ONU
          
          // Extraindo o id do contrato
          const idContratoExtraido = dadosCliente?.data[0]?.id_contrato || '';
          if (idContratoExtraido) {
            console.log('🎯 ID do contrato encontrado:', idContratoExtraido);
            setIdContrato(idContratoExtraido); // Atualiza o id_contrato dinamicamente
          } else {
            console.error('⚠️ Nenhum id de contrato encontrado.');
          }

        } else {
          console.error('⚠️ Estrutura de dados não encontrada ou dados mal formatados.');
        }
      } catch (error) {
        console.error('❌ Erro ao carregar os dados do cliente do localStorage:', error);
      }
    } else {
      console.error('❌ Nenhum dado encontrado no localStorage');
    }
  };

  // Função para buscar os dados do servidor automaticamente usando o idContrato
  const fetchClientDataFromAPI = async (contratoId) => {
    if (!contratoId) {
      console.error('❌ Erro: idContrato não está definido.');
      return;
    }

    console.log('Fazendo requisição com idContrato:', contratoId);
    try {
      const response = await fetch('https://www.appmax.nexusnerds.com.br/buscar-radpop', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          query: contratoId, // Passa o id_contrato corretamente
        }),
      });

      if (response.ok) {
        const data = await response.json();
        console.log('✅ Dados obtidos do servidor:', data);

        // Salva os dados no localStorage
        localStorage.setItem('dadosCliente', JSON.stringify(data));

        // Atualiza o estado com os dados do cliente
        setClientData(data.data); // Carrega os dados da resposta
      } else {
        console.error('❌ Erro ao buscar os dados do servidor:', response.statusText);
      }
    } catch (error) {
      console.error('❌ Erro ao fazer requisição:', error);
    }
  };

  // Função para decidir se faz a requisição ou carrega do localStorage
  const loadData = () => {
    const storedData = localStorage.getItem('dadosCliente');
    if (!storedData) {
      console.log('Nenhum dado no localStorage, buscando dados da API...');
    } else {
      console.log('Carregando dados do localStorage...');
      mostrarDadosDoLocalStorage(); // Chama a função para carregar o ID
    }
  };

  useEffect(() => {
    loadData(); // Carrega os dados do localStorage e obtém o ID do contrato

    // Se o idContrato já estiver definido, busca os dados da API
    if (idContrato) {
      fetchClientDataFromAPI(idContrato);
    }
  }, [idContrato]);

  return (
    <div>
      <Box sx={{ padding: '20px' }}>
        <Button
          variant="contained"
          startIcon={<ArrowBack />}
          onClick={() => navigate(-1)}
          sx={{
            marginBottom: '20px',
            backgroundColor: '#05C53B',
            color: '#fff',
            padding: '10px 10px',
            borderRadius: '20px',
            fontSize: '12px',
            fontWeight: 'bold',
            boxShadow: '0px 4px 6px rgba(0, 0, 0, 0.1)',
            '&:hover': {
              backgroundColor: '#0277bd',
            },
          }}
        >
          Voltar
        </Button>

        <Typography variant="h5" gutterBottom>
          Detalhes do Cliente
        </Typography>

        {/* Verifica se clientData está disponível e exibe os dados */}
        {clientData && clientData.length > 0 ? (
          clientData.map((onuItem, index) => (
            <Paper
              key={index}
              elevation={3}
              sx={{ marginBottom: '15px', padding: '20px', borderRadius: '10px', backgroundColor: '#f0f0f0' }}
            >
              <Grid container spacing={2} alignItems="center">
                <Grid item xs={12}>
                  <Typography variant="body1">
                    <strong>Nome:</strong> {onuItem.nome}
                  </Typography>
                  <Typography variant="body1">
                    <strong>MAC:</strong> {onuItem.mac}
                  </Typography>
                  <Typography variant="body1">
                    <strong>Sinal RX:</strong> {onuItem.sinal_rx} dBm
                  </Typography>
                </Grid>
              </Grid>
            </Paper>
          ))
        ) : (
          <Typography variant="body2" color="textSecondary">
            Nenhum dado de cliente encontrado.
          </Typography>
        )}
      </Box>
    </div>
  );
}

export default DetalhesCliente;
